body {
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

navbar {
  background-color: #333;
  overflow: hidden;
}

navbar a {
  float: left;
  color: #f2f2f2;
  text-align: center;
  padding: 14px 16px;
  font-size: 17px;
  text-decoration: none;
}

navbar a:hover {
  color: #000;
  background-color: #ddd;
}

.container {
  height: calc(100vh - 60px);
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  display: flex;
}

.left {
  max-width: 500px;
  width: 100%;
  background-color: #f2f2f2;
  border-radius: 10px;
  flex-direction: column;
  align-items: center;
  margin: 10px;
  padding: 20px;
  display: flex;
  box-shadow: 0 0 10px #0000001a;
}

.name {
  margin: 0;
  padding: 0;
  font-size: 24px;
  font-weight: bold;
}

.img {
  width: 200px;
  height: 200px;
  background-color: #ccc;
  border-radius: 50%;
  margin-top: 20px;
}

.posts {
  max-width: 500px;
  width: 100%;
  background-color: #f2f2f2;
  border-radius: 10px;
  margin: 10px;
  padding: 20px;
  box-shadow: 0 0 10px #0000001a;
}

h3 {
  margin: 0;
  padding: 0;
  font-size: 24px;
  font-weight: bold;
}

